import { Component, OnInit } from '@angular/core';
import { FormArray, FormControl, FormGroup, Validators, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import Swal from 'sweetalert2';
import { ServiceService } from '../services/service.service';
declare var iziToast:any;
declare var $:any;
@Component({
  selector: 'app-create-category',
  templateUrl: './create-category.component.html',
  styleUrls: ['./create-category.component.css']
})
export class CreateCategoryComponent implements OnInit {
  
  addcategory:FormGroup;
  addsubcategory:FormGroup;
  category_name;
  subcategory_name;
  category_id;
  categorylists;
  myid;
  selected;
  catselect;
  category_list;
  edit_cat_id;
  constructor(private fb: FormBuilder,private serverService: ServiceService, private router:Router) {
    this.addcategory = this.fb.group({
      category_name: ['', Validators.required],
      description: ['',Validators.required]
      });
    this.addsubcategory = this.fb.group({
      subcategory_name: ['', Validators.required],
    });
   }
   
  ngOnInit(): void {
    // Swal.close();    
this.getcategory();
  }
  cancel1() {
    // alert('123')
    this.addcategory.reset();
   }
  cancel() {
    // alert('123')
    this.addsubcategory.reset();
    $(".form-control").val('');
    this.serverService.GoBack();

   }
  postD2(addcategory){
    var category_name = addcategory.value.category_name;
    addcategory.value.description= addcategory.value.description.replace( /[\r\n]+/gm, " " );
    console.log(addcategory.value.description)
    Swal.fire({
      html:
        '<div style="display: flex;justify-content: center;"><div class="pong-loader"></div></div>',
      showCloseButton: false,
      showCancelButton: false,
      showConfirmButton: false,
      focusConfirm: false,
      background: 'transparent'
    });
    let api_req:any = '{"operation": "category","moduleType": "category","api_type": "web","element_data": {"action":"category","category_name":"'+category_name+'","description":"'+addcategory.value.description+'"}}';
    this.serverService.sendserver(api_req).subscribe((response: any)=>{
         Swal.close();      
        if(response.result.data == 1){
          iziToast.success({
            message: "Category created successfully",
            position: 'topRight'
        });
        this.addcategory.reset();
this.getcategory();

        }else{
          iziToast.error({
            message: "Category already exist",
            position: 'topRight'
        });
        }
    },(error: any)=>{
        console.log(error)
    });
    console.log(addcategory);
  }
  
  getcategory(){
    let api_req:any = '{"operation": "category","moduleType": "category","api_type": "web","element_data": {"action":"selectcategory"}}';
    this.serverService.sendserver(api_req).subscribe((response: any)=>{
      console.log(response)
      if(response.status == true){
        // console.log("asdf")
        this.categorylists = response.result.data;
      }
  },(error: any)=>{
      console.log(error)
  });
 
}
  
  postA2(addsubcategory){
    var subcategory_name = addsubcategory.value.subcategory_name;
    
    let api_req:any = '{"operation": "category","moduleType": "category","api_type": "web","element_data": {"action":"sub_category","sub_category_name":"'+subcategory_name+'","category_id":"'+this.catselect+'"}}';
    Swal.fire({
      html:
        '<div style="display: flex;justify-content: center;"><div class="pong-loader"></div></div>',
    showCloseButton: false,
      showCancelButton: false,
      showConfirmButton: false,
      focusConfirm: false,
      background: 'transparent',


    });
    this.serverService.sendserver(api_req).subscribe((response: any)=>{
       Swal.close();
        if(response.result.data == 1){
          // Swal.close(); 
          iziToast.success({
            message: "SubCategory created successfully",
            position: 'topRight'
        });
        this.addsubcategory.reset();

          //this.router.navigate(['/videoupload'])
          // Swal.close();
        }else if(response.result.data == 2){
          iziToast.error({
            message: "SubCategory already exist",
            position: 'topRight'
        });
        }
        // Swal.close();
    },(error: any)=>{
        console.log(error)
    });
    console.log(addsubcategory);
  }

  deleteCategory(id){
    Swal.fire({
      title: 'Are you sure?',
      text: "It will be Permanently remove all the article belongs to this Category",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes, delete it!'
    }).then((result) => {
      if (result.value) {
  let api_req:any = '{"operation": "category","moduleType": "category","api_type": "web","element_data": {"action":"delete_category","id":"'+id+'"}}	';
  
  this.serverService.sendserver(api_req).subscribe((response:any) => {
    if(response.result.data==1){
      Swal.fire(
        'Deleted!',
        'success'
      );
     this.getcategory();
    }
  
  }, 
  (error)=>{
      console.log(error);
  });
      }
    })
  }

  ToggleStatus(queue){
    let data;
    let status;
    if(queue.status=='1'){
      
      data='Make category '+queue.category_name+' inactive ';
      status='0';
    }
    else{

      data='Make category '+queue.category_name+' active ';
      status='1';

    }
  
    Swal.fire({
      title: data,
      text: "It belongs to the whole category of articles",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes'
    }).then((result) => {
      if (result.value) {
        let api_req:any = '{"operation": "category","moduleType": "category","api_type": "web","element_data": {"action":"toggle_category_status","id":"'+queue.id+'","status":"'+status+'"}}	';
  
  this.serverService.sendserver(api_req).subscribe((response:any) => {
    if(response.result.data==1){
      Swal.fire(
        'Updated!',
        'success'
      );
     this.getcategory();
    }
  
  }, 
  (error)=>{
      console.log(error);
  });
      }
    })
  }

  edit_category(id){
    
    let api_req:any = '{"operation": "category","moduleType": "category","api_type": "web","element_data": {"action":"editCategory","id":"'+id+'"}}';
    this.serverService.sendserver(api_req).subscribe((response: any)=>{
      console.log(response)
      if(response.status == true){
        // console.log("asdf")
        // this.categorylists = response.result.data;
        $('#edit_category_name').val(response.result.data[0].category_name);
        $('#edit_category_description').val(response.result.data[0].description);
        $('#edit_category').modal('show');       
this.edit_cat_id=response.result.data[0].id;
      }
  },(error: any)=>{
      console.log(error)
  });
  console.log();
}
Updatecategory(){
   let name=$('#edit_category_name').val();
  let description= $('#edit_category_description').val();

  if(description==''||name==''){
    iziToast.warning({
      message: "Please enter the details",
      position: 'topRight'
  });
  return false
  }
  description= description.replace( /[\r\n]+/gm, " " );

  let api_req:any = '{"operation": "category","moduleType": "category","api_type": "web","element_data": {"action":"Updatecategory","id":"'+this.edit_cat_id+'","category_name":"'+name+'","description":"'+description+'"}}';
  this.serverService.sendserver(api_req).subscribe((response: any)=>{
    console.log(response)
    if(response.result.data==true){
      $('#edit_category').modal('hide');       

      Swal.fire(
        'Updated!',
        'success'
      );
     this.getcategory();
    }else{
      Swal.fire(
        'Oops!',
        'error'
        );
    }
},(error: any)=>{
    console.log(error)
});
 
}
}
